	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Change of Channels","date":"21 May 2001","slug":"/2001-05-21-change-of-channels/","tags":"Y2001"},"html":"<p><a href=\"http://www.schockwellenreiter.de\">The Shockwave Rider</a> can now be reached at a new frequency: http://www.schockwellenreiter.de. Not all features have been transfered over yet, but my favorite is already there: Jörg's rollover red-green tie.</p>"},"next":{"fields":{"title":"In The Bath","date":"20 May 2001","slug":"/2001-05-20-in-the-bath/"}},"previous":{"fields":{"title":"Winds of Change","date":"22 May 2001","slug":"/2001-05-22-winds-of-change/"}}},{"node":{"fields":{"title":"In The Bath","date":"20 May 2001","slug":"/2001-05-20-in-the-bath/","tags":"Y2001"},"html":"<p><a href=\"http://static.userland.com/sh4/gems/mausnews/bath.rm\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/bathrm.jpg\" height=\"187\" width=\"181\" border=\"0\" alt=\"bathrm.jpg: \" /></a></p>\n<p><a href=\"http://static.userland.com/sh4/gems/mausnews/bath.rm\">In the bath</a>: a pitiful first attempt at uploading home video for the web (30 seconds for Real Player, 163 KB). It's going to take a while for me to learn digital video. Maybe I need a 'Dummies' book. <b>Later:</b> It worked! Christopher's grandparents were able to view the video.</p>\n<p><b>Helping out family in Minnesota</b></p>\n<p>My mother writes (I've removed the names): \"Dad has been very busy this past week.  His sister-in-law's brother had a heart attack about a month ago so was unable to plant his crops.  He has about a 1000 acres.  Dad, two of his brothers along with a couple of neighbors and a son got going on Wed. and Thurs. and it sounded like they would finish up Friday. I think they meant all of it.  Then after a few weeks, the neighbor will spray for weeds and then they are set till harvest.\"</p>\n<p><b>And finally in soccer</b></p>\n<p>Hamburg's inner-city team <a href=\"http://www.fcstpauli.de\">FC St. Pauli</a> has promoted to the Bundesliga 1st league for the 4th in 12 years (which means they've been demoted to the 2nd league 3 times in that same time period).  I imagine that in a year or two they'll be back in the 2nd league, but it will be fun to have them in the big league for a while, if nothing else than to watch overpaid teams like Bayern Munich play in their dinky neighborhood stadium.</p>"},"next":{"fields":{"title":"The Mug Game","date":"19 May 2001","slug":"/2001-05-19-the-mug-game/"}},"previous":{"fields":{"title":"Change of Channels","date":"21 May 2001","slug":"/2001-05-21-change-of-channels/"}}},{"node":{"fields":{"title":"The Mug Game","date":"19 May 2001","slug":"/2001-05-19-the-mug-game/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/muggame.jpg\" height=\"224\" width=\"300\" border=\"0\" alt=\"muggame.jpg: \" /></p>\n<p>Christopher has invented a new game. It requires 2 coffee mugs and 3 spoons. The idea is to move the spoons from mug to mug. The game is somewhat noisy.</p>"},"next":{"fields":{"title":"Vacation Plans","date":"17 May 2001","slug":"/2001-05-17-vacation-plans/"}},"previous":{"fields":{"title":"In The Bath","date":"20 May 2001","slug":"/2001-05-20-in-the-bath/"}}}],"pathPrefix":"","first":false,"last":false,"index":1003,"pageCount":1101}}

